
body {
    margin: 0%;
}
/* Encabezado */
.header{
    margin: 0%;
    padding: 0;
    width: 100%;
    height: 10vh;
    min-height: 100px;

    background-color: #700505 ;
    color:#E5E7E9 ;
    
    display: -webkit-box;
    
    display: -ms-flexbox;
    
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
/* Distancia entre el borde y el titulo (NU Bank) */
.header h1{
    width: 20%;
    text-align: center;
}
.header nav {
    height: 100%;
    /* Espacio entre botones */
    width: 45%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 20px;
}
.header nav button{
    /* Color letra de botón; del botón; del borde */
    color: #fff;
    display: inline-block;
    border: 5px solid transparent;
    line-height: 1.5;
    margin-top: .25rem;
    margin-bottom: .25rem;
    font-weight: 1000;
    border-radius: 2em;
    padding: .375rem .75rem;
    background-color: #ad1010;
    border-color: #a03737;
    width: 180px;
    
    /* Redondes del borde del botón; ancho y alto */
    border-radius: 5px;
    padding: 10px 20px;
}
.header nav button:hover{
    color: #700505;
    background: #E5E7E9;
    border: 1px solid #E5E7E9;
}
.main-component{
    height: 75vh;
